import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  getDataNoTimeOut,
  formatDate,
  moment,
  ToastNotification,
  PanelBody,
} from "../../../../components/helpers/library";
import HeadLaporanStockHutang from "./HeadLaporanStockHutang";

class LaporanStockHutang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanStockHutang: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    getDataNoTimeOut(
      "hutang/report-stock-hutang?tgl_from=" +
        data_post.tgl_awal +
        "&tgl_to=" +
        data_post.tgl_akhir
    )
      .then((res) => {
        // console.log(res);
        if (res.data.length === 0) {
          ToastNotification("info", "Laporan Stock Hutang Kosong");
          this.setState({
            export: false,
            isLoading: false,
          });
          return false;
        }
        ToastNotification("success", "Laporan Stock Hutang Tersedia");
        this.setState({
          LaporanStockHutang: res.data,
          isLoading: false,
          export: true,
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Stock Hutang</li>
        </ol>
        <h1 className="page-header">Laporan Stock Hutang </h1>
        <Panel>
          <PanelHeader>Laporan Stock Hutang</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadLaporanStockHutang
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanStockHutang={this.state.LaporanStockHutang}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
      </div>
    );
  }
}

LaporanStockHutang = reduxForm({
  form: "LaporanStockHutang",
  enableReinitialize: true,
})(LaporanStockHutang);
export default connect(null)(LaporanStockHutang);
